  overflow: hidden;
  margin: 10px 15px 0;
  box-shadow: none;
}
#index .main .warp .news-wrap>div{
  height: 30px;
  margin: 0;
  box-shadow: none;
}
.news-one{
  color: #111;
  text-decoration: none;
  font-size: 14px;
  line-height: 30px;
  text-align: left;
  display: flex;
  flex-direction: row;
}
.news-one:hover, .news-one:active, .news-one:hover .news-date, .news-one:active .news-date{
  color: #69c;
}
.news-text{
  display: inline-block;
  vertical-align: middle;
  /* width: 40%; */
  /* width: 16em; */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex: 3;
}
.news-date{
  text-align: right;
  color: #999;
  font-size: 14px;
  /* margin-left: 50px; */
  flex: 1;
}
#index .main .warp .news-wrap>div.move{
  margin-top: -30px;
  transition: all 2s ease;
}
</style>



