.mark {
  float: left;
  width: calc(100% - 360px);
  height: 100%;
}

.mark .topbar {
  color: #333333;
  font-size: 13px;
  height: 77px;
  padding: 0px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mark .topbar span {
  display: flex;
}

.mark .topbar span a {
  text-decoration: none;
  color: #666;
}

.mark .topbar img {
  width: 12px;
  height: 15px;
  margin-top: 2px;
}

.mark .topbar span:nth-child(1) img:nth-child(1) {
  margin-left: 10px;
  margin-right: 10px;
}

.mark .topbar span:nth-child(1) img:nth-child(2) {
  margin-left: 10px;
  margin-right: 10px;
}

.mark .topbar span:nth-child(2) a img {
  margin-left: 10px;
  margin-bottom: -2px;
}

.mark .content {
  background: #fff;
  height: calc(100% - 97px);
  margin: 0 40px;
  overflow-y: scroll;
}

.menu {
  float: right;
  width: 360px;
  height: 100%;
  background: #fff;
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.1);
}

.menu .title {
  color: #393C40;
  font-size: 18px;
  margin: 21px 0 21px 25px;
}

.menu .row input {
  display: none;
}

.menu .row input[type=radio]:checked + label {
  background:linear-gradient(-135deg,rgba(163,137,212,1),rgba(137,158,212,1));
  color: #fff;
}

.menu .row {
  display: inline-block;
  margin: 0 0 25px 20px;
}

.menu .row:nth-child(3n + 1) {
  margin-left: 25px;
}

.menu .row label {
  display: block;
  width: 90px;
  height: 40px;
  line-height: 40px;
  border: 1px solid #9B9B9B;
  border-radius: 24px;
  text-align: center;
  color: #9B9B9B;
}

.menu .content2 {
  display: flex;
  flex-wrap: wrap;
  padding: 0 25px;
}

.menu .row2 {
  display: flex;
  min-width: 87px;
  margin-bottom: 25px;
}

.menu .row2:nth-child(3n + 2) {
  margin: 0 24px;
}

.menu .row2 input {
  display: none;
}

.menu .row2 input[type=checkbox] + label {
  /* display: inline-block; */
  width: 19px;
  height: 19px;
  border: 1px solid #EAEAEA;
  border-radius: 5px;
  margin-right: 10px;
}

.menu .row2 input[type=checkbox]:checked + label {
  /* display: inline-block; */
  width: 19px;
  height: 19px;
  border: 1px solid #fff;
  /* background:linear-gradient(-135deg,rgba(29,233,182,1),rgba(29,196,233,1)); */
  border-radius: 5px;
  margin-right: 10px;
  background-image: url(../assets/sure.png);
  background-repeat: no-repeat;
  background-size: 19px 19px;
}

.menu .row2 .label2 {
  /* display: inline-block; */
  color: #333;
  font-size: 14px;
}

.sure {
  width:270px;
  height:48px;
  line-height: 48px;
  text-align: center;
  color: #fff;
  background:linear-gradient(-135deg,rgba(29,233,182,1),rgba(29,196,233,1));
  border-radius: 24px;
  margin: 0 auto;
}

/*  */
.report {
  padding: 0 40px;
}

.report .title {
  text-align: center;
  color: #393C40;
  font-size: 20px;
  font-weight: bold;
  margin: 32px 0;
}

.report .box {
  display: grid;
  grid-column-gap: 1px;
  grid-row-gap: 1px;
  grid-template-columns: 1fr 5fr;
}

.report .box > div {
  outline: 1px solid #D3D3D3;
  padding: 15px;
}

.report .box > div:nth-child(3) {
  /* grid-row: 2/5; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.report .box > div:nth-child(4) {

}

.report .box > div:nth-child(1),
.report .box > div:nth-child(3),
.report .box > div:nth-child(5) {
  font-size: 14px;
  color: #333;
  text-align: center;
  font-weight: bold;
}

.report .box > div:nth-child(2),
.report .box > div:nth-child(4),
.report .box > div:nth-child(6) {
  color: #666;
  font-size: 14px;
}

.report .box > div:nth-child(1),
.report .box > div:nth-child(2),
.report .box > div:nth-child(5),
.report .box > div:nth-child(6) {
  background: #E9F9FF;
}

.chartbox .title2 {
  font-size: 16px;
  color: #333;
  font-weight: bold;
  margin: 30px 0;
}

.chartbox .info {
  font-size: 12px;
  color: #5F5F5F;
  margin: 0 20px 20px 20px;
}

.chart {
  /* width: 1100px; */
  width: 100%;
  height: 300px;
}
</style>



